html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

@font-face {
  font-family: LeagueSpartanBold;
  src: url("../fonts/LeagueSpartan-Bold.woff2") format("woff2"), url("../fonts/LeagueSpartan-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  width: 1225px;
  margin: 0 auto;
}

.block-title {
  text-align: center;
}

.block-title__up {
  display: block;
  color: #5da8ef;
  font-family: LeagueSpartanBold, sans-sarif;
  font-size: 9px;
  text-transform: uppercase;
  letter-spacing: 5.4px;
}

.block-title__name {
  color: #333333;
  font-family: LeagueSpartanBold, sans-sarif;
  font-size: 42px;
  font-weight: 700;
  letter-spacing: 0.42px;
}

.header {
  margin-bottom: 45px;
}

.header__inner {
  display: flex;
}

.header__nav {
  margin-left: 191px;
}

.nav {
  display: flex;
}

.nav li {
  margin-right: 45px;
}

.nav a {
  text-decoration: none;
  font-family: LeagueSpartanBold, sans-sarif;
  color: #0b0c11;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.surfboards__list {
  display: flex;
  justify-content: space-between;
}

.surfboard__description {
  display: inline-block;
}

.surfboard__description span {
  display: block;
}

.surfboard__kind {
  color: #7a7a7a;
  font-family: LeagueSpartanBold, sans-sarif;
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2.4px;
}

.surfboard__model {
  font-size: 26px;
  color: #333;
  font-family: LeagueSpartanBold, sans-sarif;
}

.surfboard__price {
  color: #ce60a8;
  font-size: 20px;
  font-weight: 300;
}

.surfboard__btn {
  text-decoration: none;
  color: #333333;
  font-size: 9px;
  font-weight: 700;
  font-family: LeagueSpartanBold, sans-sarif;
  text-transform: uppercase;
  letter-spacing: 5.4px;
}

.surfboard__buy {
  text-align: center;
}

.surfboard__img {
  display: inline-block;
}

.main-slider {
  margin-bottom: 220px;
}

.slide {
  position: relative;
}

.slide__description {
  position: absolute;
  top: 50%;
  z-index: 99;
}

.slide__img {
  margin-left: 207px;
}

.slide__title {
  display: block;
  font-family: LeagueSpartanBold, sans-sarif;
  color: #0b0c11;
  font-size: 62px;
}

.slide__text {
  color: #8f8f8f;
  font-family: LeagueSpartanBold, sans-sarif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.35px;
  width: 400px;
}
